import Spinner from "components/spinner/spinner.component";
import { useEffect, useReducer } from "react";
import agent from "../../services/agent.service";
import { Dialog } from "primereact/dialog"
import { acceptApplicant, viewApplicant } from "store/modules/job";
import { useDispatch, useSelector } from "react-redux";

const ViewAspirantModal = ({ showModal, onHide, jobId }) => {

    const dispatch = useDispatch();
    const applicants = useSelector(state => state.job.applicants);
    const loadingApplicants = useSelector(state => state.job.loadingApplicants)


    useEffect(() => {
        if (jobId) {
            console.log('job id', jobId)
            dispatch(viewApplicant(jobId))
        }
    }, [jobId])

    const handleAcceptApplicant = (applicantId) => {
        const data = {"jobId": jobId}
        dispatch(acceptApplicant(applicantId, data));
    }

    return (<Dialog
        visible={showModal}
        onHide={onHide}
        breakpoints={{ "960px": "75vw" }}
        style={{ width: "50vw" }}
    >
        <div className="modal-body">
            {loadingApplicants ? <Spinner /> : (<div>
                {applicants && !applicants.length && <div className="d-flex justify-content-center"><h4>No applicants yet</h4></div>}
                {applicants && applicants.length && <h4>{applicants.length} Applicant(s)</h4>}
               { applicants.map((applicant, index) => <div key={index} className="p-card mt-2 p-2">
                    <div className="d-flex">
                        <div className="image" style={{marginRight: '10px'}}>
                            <img src="data:image/jpeg;base64," style={{borderRadius: '50%',width: '50px',height: '50px'}} />
                        </div>
                        <div className="content">
                            <h4>{applicant.name}</h4>
                            <p>{applicant.phoneNumber} | {applicant.location}</p>
                            <div className="d-flex justify-content-start mt-4">
                                <button onClick={() => handleAcceptApplicant(applicant.accountId)} className="btn btn-sm btn-primary mr-2" style={{backgroundColor: '#9B50E1', border: 'none'}}>Accept</button>
                                <button className="btn btn-sm btn-primary mr-2" style={{backgroundColor: '#9B50E1', border: 'none'}}>View Profile</button>
                                <button className="btn btn-sm btn-primary mr-2" style={{backgroundColor: '#9B50E1', border: 'none'}}>Message</button>
                                <i className="pi pi-trash align-self-center mr-2" style={{ 'fontSize': '1.4em', color: 'red', cursor: 'pointer' }} ></i>
                            </div>
                        </div>
                    </div>
                </div>)}
            </div>)}
           
        </div>
    </Dialog>)
}

export default ViewAspirantModal